// Admin pages
import home from 'src/components/CCSpaceComponents/home.vue'
import about from 'src/components/CCSpaceComponents/about.vue'
import contact from 'src/components/CCSpaceComponents/contact.vue'
import termsConditions from 'src/components/CCSpaceComponents/termsConditions.vue'
import privacyPolicy from 'src/components/CCSpaceComponents/privacyPolicy.vue'
//Create
import createLayout from 'src/components/CCSpaceComponents/createComponents/create.vue'
import connectLayout from 'src/components/CCSpaceComponents/createComponents/connect.vue'
import scheduleLayout from 'src/components/CCSpaceComponents/createComponents/schedule.vue'
import specializeLayout from 'src/components/CCSpaceComponents/createComponents/specialize.vue'

import fulltimemembershipLayout from 'src/components/CCSpaceComponents/createComponents/fulltimemembership.vue'
import createthankyou from 'src/components/CCSpaceComponents/createComponents/createthankyou.vue'
import instagram from 'src/components/CCSpaceComponents/instagram.vue'

const routes = [

  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/about',
    name: 'about',
    component: about
  },
  {
    path: '/contact',
    name: 'contact',
    component: contact
  },
  {
    path: '/termsConditions',
    name: 'termsConditions',
    component: termsConditions
  },
  {
    path: '/privacyPolicy',
    name: 'privacyPolicy',
    component: privacyPolicy
  },
  {
    path: '/create', // Create Screens
    name: 'createLayout',
    component: createLayout
  },
  {
    path: '/createthankyou', // Create Screens
    name: 'createthankyou',
    component: createthankyou
  },
  {
    path: '/connect', // Create Screens
    name: 'connectLayout',
    component: connectLayout
  },
  {
    path: '/schedule', // Create Screens
    name: 'scheduleLayout',
    component: scheduleLayout
  },
   {
    path: '/fulltimemembership', // Create Screens
    name: 'fulltimemembershipLayout',
    component: fulltimemembershipLayout
  },
   {
    path: '/specialize', // Create Screens
    name: 'specializeLayout',
    component: specializeLayout
  },

  
  {
    path: '/instagram', // Create Screens
    name: 'instagram',
    component: instagram
  }
  
]

export default routes



